import Head from "next/head";
import styled from "styled-components";
import hash from "object-hash";
import { articles, Article as ArticleType } from "../data/articles";
import { NavBar, Article, Footer } from "../components";

export default () => (
  <Centered>
    <Head>
      <title>WebAssembly News</title>
    </Head>
    <NavBar />
    <Articles articles={articles} />
  </Centered>
);

const Centered = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
`;

const Articles = ({ articles }: { articles: ArticleType[] }) => (
  <ContentArea>
    <ArticleColumns>
      {articles.map(article => (
        <Article key={hash(article)} data={article} />
      ))}
    </ArticleColumns>
    <Footer />
  </ContentArea>
);

const ArticleColumns = styled.div`
  display: grid;
  grid-gap: 10px;
  grid-row-gap: 30px;
  grid-column-gap: 50px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
`;

const ContentArea = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  padding-left: 5%;
  padding-right: 5%;
`;



