import styled from "styled-components";
import Link from "next/link";
import { withRouter } from "next/router";
import SearchIcon from "react-icons/lib/fa/search";

const labels = [
  /* "Technology", "Events", "Community",  "Overview",  */
  "News"
  /*"Events",
   "Apps",
  "Code",*/
];

export default withRouter(() => (
  <NavBarArea>
    <Column>
      <Row>
        <Link href="/">
          <Logo alt="WebAssembly logo" src="/static/webassembly-banner.svg" />
        </Link>
        <Spacer />
        {/* <NavButtons labels={labels} activeIndex={0} /> */}
      </Row>
      {/*  <SearchBox /> */}
    </Column>
  </NavBarArea>
));

const SearchBox = () => (
  <SearchBoxArea>
    {/* <Buttons>
      {labels.map(label => <Button key={label}>{label}</Button>)}
    </Buttons> */}
    <SearchIcon
      size={22}
      color="rgba(255, 255, 255, 0.5)"
      style={{ margin: 8 }}
    />
  </SearchBoxArea>
);

const SearchBoxArea = styled.div`
  padding: 6px;
  display: flex;
  flex: 1;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.05);
  border-width: 0px;
  margin-top: 1%;
  margin-bottom: 3%;
  flex-flow: row wrap;
  align-items: center;
`;

export const NavBarArea = styled.div`
  width: 90%;
  margin-top: 5%;
  margin-left: 5%;
  margin-right: 5%;
`;

const Logo = styled.img`
  /* border-style: solid; */
  max-width: 400px;
  width: 90%;
  height: 100%;
`;

const NavButtons = ({
  labels,
  activeIndex
}: {
  label: string[],
  activeIndex: number
}) => (
  <NavButtonsArea>
    {labels.map((label, index) => (
      <Link href={`/${label.toLowerCase()}`}>
        {index === activeIndex ? (
          <ActiveNavButton key={index}>{label}</ActiveNavButton>
        ) : (
          <NavButton key={index} onClick={() => console.log("click")}>
            {label}
          </NavButton>
        )}
      </Link>
    ))}
  </NavButtonsArea>
);

const Column = styled.div`
  display: flex;
  flex-direction: column;
`;

const Row = styled.div`
  display: flex;
  margin-top: 2%;
  margin-bottom: 2%;
  width: 100%;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  align-items: flex-end;
`;

const Spacer = styled.div`
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: flex-end;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 10px;
`;

const NavButtonsArea = styled.div`
  font-size: 18px;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 40px;
`;

const ActiveNavButton = styled.a`
  margin-right: 20px;
  padding: 5px;
  color: rgba(255, 255, 255, 0.9);
`;

const NavButton = styled.a`
  margin-right: 30px;
  padding: 5px;
`;

const Button = styled.div`
  color: rgba(255, 255, 255, 0.75);
  font-size: 16px;
  background-color: rgba(255, 255, 255, 0);
  border-style: solid;
  border-width: 2px;
  border-color: #6152e7;
  margin-right: 10px;
  padding: 9px;
  padding-left: 10px;
  padding-right: 10px;
  min-width: 50px;
  border-radius: 8px;
  text-align: center;
`;

const Buttons = styled.div`
  display: flex;
  flex: 1;
  flex-flow: row wrap;
  justify-content: flex-start;
`;



